import {debounce} from 'common/js/util';
export default {
  data(){
    return {
      showFlag:false,
      timer: null
    }
  },
  props: {
    content: {
      type: String,
      default: ''
    }
  },
  methods: {
    setShow(flag){
      let time = flag || 1500;
      this.showFlag = true;
      if(this.timer){
          clearTimeout(this.timer);
      }
      this.timer=setTimeout(()=>{
          this.showFlag=false;
      },time)

    },
    show(){
      this.showFlag = true;
    },
    hide(){
      this.showFlag = false;
    }
  }
}



